<template>
  <div class="animated fadeIn">
    <b-tabs content-class="bg-white" class="bg-white">
      <b-tab active>
        <template #title>
          <span class="d-md-down-none">Ticket/s </span>By Owner
        </template>
        <server-side-paginated-table
                :fields="table_settings3.fields"
                :api_url="table_settings3.api_url"
                :delete_url="table_settings3.delete_url"
                ref="displayTable3"
                :top_right_btn_redirect="false"
        />
      </b-tab>
      <b-tab>
        <template #title>
          <span class="d-md-down-none">Ticket/s </span>By Batch
        </template>
        <by-batch-table
                :fields="table_settings.fields"
                :api_url="table_settings.api_url"
                :delete_url="table_settings.delete_url"
                ref="displayTable"
                :top_right_btn_redirect="false"
        />
      </b-tab>
      <b-tab>
        <template #title>
          <span class="d-md-down-none">Ticket/s </span>Single
        </template>
        <server-side-paginated-table
                :fields="table_settings2.fields"
                :api_url="table_settings2.api_url"
                :delete_url="table_settings2.delete_url"
                ref="displayTable2"
                :top_right_btn_redirect="false"
        />
      </b-tab>
      <b-tab>
        <template #title>
          <span class="d-md-down-none">Ticket/s </span>Transfer
        </template>
        <b-row>
          <b-col cols="12" md="6" lg="4">
            <div class="card bg-light m-3">
              <div class="card-header bg-dark text-white" id="capture">
                <h6 class="mb-0"><font-awesome-icon icon="plus-circle"/> Generate Concert Ticket/s</h6>
              </div>
              <div class="card-body bg-white">

                <b-row align-v="center" class="loader" v-if="is_loading">
                  <b-col>
                    <b-spinner></b-spinner>
                    <br><br>
                    <strong>Loading</strong>
                  </b-col>
                </b-row>
                <b-form @submit="transfer">
                  <b-form-group
                          label="Transfer To:"
                          description="This field is required"
                  >
                    <select class="form-control" v-model="form.owner_id" required>
                      <option value="">Please Select</option>
                      <option value="-1">New Record</option>
                      <option v-for="row in owners" :value="row.id">
                        {{row.name}}
                      </option>
                    </select>
                  </b-form-group>
                  <b-form-group
                          label="Name:"
                          v-if="form.owner_id=='-1'"
                  >
                    <b-form-input
                            v-model="form.owner_name"
                            type="text"
                            description="This field is required"
                            required
                    ></b-form-input>
                  </b-form-group>

                  <b-form-group
                          label="Ticket Number (From):"
                          description="This field is required"
                  >
                    <b-form-input
                            v-model="form.from"
                            type="number"
                            required
                            min="1000001"
                    ></b-form-input>
                  </b-form-group>

                  <b-form-group
                          label="Ticket Number (To):"
                          description="This field is required"
                  >
                    <b-form-input
                            v-model="form.to"
                            type="number"
                            required
                            min="1000001"
                            :max="Number(form.from+99)"
                    ></b-form-input>
                  </b-form-group>
                  <div class="text-right">
                    <b-button type="submit" variant="dark" size="sm"><font-awesome-icon icon="save" /> Transfer Ticket/s</b-button>
                  </div>
                </b-form>
              </div>
            </div>
          </b-col>
        </b-row>
      </b-tab>
    </b-tabs>
    <b-modal
            v-model="details"
            centered
            size="xl"
            body-class="p-0"
            :hide-footer="true"
            :hide-header="true"
    >
        <div class="card bg-light mb-3">
          <div class="card-header bg-dark text-white d-flex flex-row-reverse align-items-center justify-content-between text-right"
          >
            <b-button type="button" @click="downloadImages()" variant="facebook" size="sm"><font-awesome-icon icon="save" /> PREVIEW & DOWNLOAD</b-button>
            <b-button type="button" @click="print()" variant="facebook" size="sm"><font-awesome-icon icon="print" /> PRINT</b-button>
          </div>
          <div class="card-body bg-white" style="overflow-x: auto;">

            <div class="text-center w-100 mb-3">
              <nav v-if="list.total>0">
                <b-pagination
                        @input="getAll"
                        align="right"
                        :total-rows="list.total"
                        :per-page="list.per_page"
                        v-model="list.current_page"
                        class="mb-0"
                >
                </b-pagination>
              </nav>
            </div>
            <b-row  v-for="(ticket, index) in tickets">
              <b-col>
                <b-row>


                  <b-col>
                    <div :id="'ticket-'+ticket.ticket_number" class="ticket-holder">
                      <span class="ticket-number">{{ticket.ticket_number}}</span>
                      <img :src="assets_path.DEFAULT_URL+'TICKET_TEMPLATE.png'" style="width:100%;border:1px solid #000;" />
                      <qrcode-vue :value="ticket.code" :size="size" level="H" style="position: absolute;top:68px;right:83px"/>
                    </div>
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
          </div>
        </div>
    </b-modal>
    <b-modal
            v-model="download"
            centered
            size="xl"
            body-class="p-0"
            :hide-footer="true"
            :hide-header="true"
    >
      <div class="card mb-0">
        <div
                class="card-header bg-dark text-white d-flex flex-row-reverse align-items-center justify-content-between text-right"
        >
          <b-button type="button" @click="downloadAll()" variant="facebook" size="sm"><font-awesome-icon icon="save" /> DOWNLOAD ALL</b-button>
        </div>
        <div class="card-body position-relative">
          <div id="qr-images">

          </div>
        </div>
      </div>
    </b-modal>
  </div>
</template>
<script>
    import {bus} from '@/main.js';
    import SwalMixin from '@/views/mixins/Swal.js'
    import { server_details } from "@/config";
    import ServerSidePaginatedTable from './ServerSidePaginatedTable.vue';
    import ByBatchTable from './ByBatchTable.vue';
    import {client_details,assets_path} from '@/config';
    import QrcodeVue from 'qrcode.vue'
    import html2canvas from 'html2canvas';
    export default {
      mixins:[SwalMixin],
      components: {ServerSidePaginatedTable,QrcodeVue,ByBatchTable},
      data: () => {
          return {
            table_settings:{
              api_url:'ticket/by-batch',
              delete_url:'',
              fields:[
                {key: 'id', label:'Batch ID'},
                {key: 'owner.name', label:'In-Charge'},
                {key: 'name', label:'Details'},
                {key: 'tickets_count', label:'Number of Ticket/s'},
              ]
            },
            table_settings2:{
              api_url:'ticket/paginated',
              delete_url:'',
              fields:[
                {key: 'ticket_number', label:'Ticket Number'},
                {key: 'ticket_batch_id', label:'Batch ID'},
                {key: 'ticket_batch.name', label:'Details'},
                {key: 'ticket_batch.owner.name', label:'In-charge'},
              ]
            },
            table_settings3:{
              api_url:'ticket/by-owner',
              delete_url:'',
              fields:[
                {key: 'name', label:'Name'},
                {key: 'ticket_batches_count', label:'Number of Batches'},
                {key: 'total_tickets', label:'Total Tickets'},
              ]
            },
            tickets:[],
            details:false,
            assets_path:{},
            size: 300,
            result:{},
            download:false,
            current_canvas_batch:1,
            list: {
              data: [],
              from: 1,
              to: 0,
              current_page: 1,
              total: 0,
              per_page: 10,
            },
            ticket_batch_id:-1,
            form:{
              owner_name:'',
              owner_id:'',
              from:1000001,
              to:''
            },
            owners:[]
          }
      },
      created(){

        this.assets_path = assets_path;

        this.getOwners();
        bus.$on('byBatchTableDbClick', (data)=>{
          this.details=true;
          this.ticket_batch_id=data.id;
          this.getAll();
          // this.axios.get('ticket/get',{params:{id:data.id}}).then(response => {
          //   this.tickets = response.data.tickets;
          //   this.result = response.data;
          // }).catch(error => console.log(error));
        });
      },
      methods: {
        downloadImage(data, filename = 'untitled.jpeg') {
          var a = document.createElement('a');
          a.href = data;
          a.download = filename;
          document.body.appendChild(a);
          a.click();
        },
        downloadAll(){
          this.tickets.forEach((element) => {
              var canvas = document.querySelector('#canvas-'+element.ticket_number);

              var dataURL = canvas.toDataURL("image/png", 1.0);
              this.downloadImage(dataURL, 'ticket-'+element.ticket_number+'.png');
          });

          //this.download=false;
        },
        downloadImages(){
          this.download=true;
          this.tickets.forEach(function(element,index) {
                const printCanvas = html2canvas(document.querySelector("#ticket-"+element.ticket_number)).then(function(canvas) {
                  canvas.id = "canvas-"+element.ticket_number;
                  document.querySelector("#qr-images").appendChild(canvas);
                });
          });
        },
        getAll() {
          this.loading = true;
          this.axios.get('ticket/paginated',{params:{ticket_batch_id:this.ticket_batch_id,page:this.list.current_page,per_page:this.list.per_page}}).then(response => {
            //console.log(result)
            this.tickets = response.data.data;
            this.list=response.data
          }).catch(error => console.log(error));
        },
        print(){
          window.open('/ticket-print/'+this.ticket_batch_id, '_blank').focus();
        },
        getOwners() {
          this.is_loading = true;
          this.axios.get('ticket/owners').then(response => {
            this.owners = response.data;
            this.is_loading = false;
          }).catch(error => console.log(error));
        },
        transfer(){
          event.preventDefault()
          this.swalConfirmSubmit((data)=>{
            if (data.value) {
              this.is_saving=true;
              this.axios.post('ticket/transfer',this.form).then(response => {
                this.swalTransactionCompleted();
              }).catch(error => {
                console.log(error)
                this.swal_transaction_error_data.text = error.response.data;
                this.swalRequestError();
              });
            }
          });
        },
      }
    }
</script>
<style>
  #qr-images canvas{
    width: 100%!important;
    height:auto!important;
  }
  .ticket-number{
    font-size: 61px;
    position: absolute;
    top: 90px;
    left: 533px;
    color: #000;
  }
  .ticket-holder{
    width: 1284px;
    position:relative
  }
  nav{
    display:inline-block;
  }
</style>